import { Component, Vue } from 'vue-property-decorator'
import {diceCup, Die} from "@/interfaces/dice";

@Component
export default class WHDiceRoller extends Vue {
  diceCup: diceCup = {dice: [], hasRolled:false};
  numberList: number[] = [1,2,3,4,5,6];
  dieClass(die:Die): any {
    return {
      '--one': die.value === 1,
      '--two': die.value === 2,
      '--three': die.value === 3,
      '--four': die.value === 4,
      '--five': die.value === 5,
      '--six': die.value === 6
    };
  }

  addDie(){
    const newDie: Die = {value: Math.floor(Math.random() * 6) + 1};
    this.diceCup.dice.push(newDie);
  }

  removeDie(){
    this.diceCup.dice.pop();
  }

  clearDice(){
    this.diceCup.dice = [];
  }

  setNumDice(numDice:number){
    if(numDice > this.diceCup.dice.length){
      while(numDice > this.diceCup.dice.length){
        this.removeDie();
      }
    } else if(numDice < this.diceCup.dice.length){
      this.addDie();
    }
  }

  rollDice(){
    this.diceCup.dice.forEach(die => {
      die.value = Math.floor(Math.random() * 6) + 1;
    });
  }

  removeAllBelow(value:number){
    this.diceCup.dice = this.diceCup.dice.filter(die => die.value < value);
  }

  get totalDice(){
    return this.diceCup.dice.length;
  }

  totalDiceAtValue(value:number){
    return this.diceCup.dice.filter(die => die.value != value);
  }
}
